/* style.css */


.body {
	width: 100%;
	background-color: #efe9e9;
}

.bloc {
	max-width: 100%;
	width: 1170px;
	min-height: 100vh;
	margin: 0 auto;
	overflow-x:hidden;
}

.conteiner_section {
	width: 100%;
	min-height: 100%;
	-ms-align-items: center;
	align-items: center;
}

  



/*  section1  */




.bloc {
	max-width: 100%;
	width: 1170px;
	min-height: 100vh;
	margin: 0 auto;
	position: relative;
}

.section1 img {
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 1;
}

.conteiner_bloc {
	-ms-align-items: center;
	align-items: center;
	z-index: 2;
}

.header {
	width: 100%;
	height: 80px;
	-ms-align-items: center;
	align-items: center;
	justify-content: space-between;
}

.logo {
	width: 50px;
	height: 50px;
	margin: 30px;
}

.logo p {
	font-size: 3em;
}

.logo span{
	font-size: 3em; 
}

.nav {
	color: #282828;
}

.nav a {
	padding: 15px;
	font-size: 1.5em; 
}

.nav a:hover {
	color: #fb8686;
}

.bl_1_h1 {
	width: 100%;
	height: 100%;
}

.bl_1_h1 h1 { 
	font-size: 6em;
	font-family: 'Exo', sans-serif;
	color: #282828;
	line-height: 1.25;
}






/*  section2  */





.section2 {
	min-height: 725px;
	overflow:hidden;overflow-x:hidden; 
}

.conteiner_bloc2 {
	position: relative;
}


.bl2_1 {
	max-width: 100%;
	width: 735px;
	min-height: 100%;
	left: 0; 
	background-color: #fff;
	position: absolute;
	z-index: 3;
}

.bl2_1 h2 {
	max-width: 90%;
	font-size: 4em;
	margin-bottom: 30px;
	font-family: "Exo";
	color: rgb(40, 40, 40);
	 
}

.bl2_1 p {
	width: 90%; 
	line-height: 1.6;
}

.bl2_1 i {
	font-size: 1.1em;
	width: 90%; 
	line-height: 1.6;
}

.bl2_2 {
	max-width: 100%;
	width: 435px;
	height: 100%;
	background-color: #ececec;
	z-index: 1;
	position: absolute;
	right: 0;
}

.bl2_2_treugol {
	max-width: 100%;
	min-height: 100%;
}
.treug {
	max-width: 100%;
	max-height: 370px; 
	z-index: 2;
	left: 0; bottom: 0;
	border: 218px solid transparent;	
	border-right: 218px solid #fb8686; 
	border-bottom: 218px solid #fb8686;
	position: absolute;
}






/*  bloc_3  */






.section3 {
	min-height: 675px;
	position: relative;
	z-index: 0;
	overflow: hidden;
}

.section3_img_1 { 
	position: absolute;
	z-index: 1;
	right: 0; bottom: 0;
	margin-bottom: -10px;
	
}

.section3_img_2 { 
	position: absolute;
	z-index: 2;
	left: 0; top: 0;
	margin-top: -10px; 
}

.section3_img_3 {
	position: absolute;
	z-index: 1;
	right: 15px; top: 10px;
} 

.bl3_1 {
	width: 100%;
	min-height: 125px;
	margin-top: 110px;
	-ms-align-items: center;
	align-items: center;
}

.bl3_1 span {
  font-size: 1.6em;
  font-family: "Exo";
  color: #ff6d6d;
  margin: 20px;
