  z-index: 5;
}

.tochca {
	color: #ff8a8a;
	margin-left: 5px;
	font-size: 10px;
}

.bl3_1 h3 {
	font-size: 40px;
	font-family: sans-serif;
	color: #282828;
	line-height: 1.25;
	margin: 0;
	padding: 0;
	z-index: 5;
}


.bl3_2 {
	width: 100%;
	justify-content: space-around;
}

.bl3_2_pbl {
	width: 33%;
	min-height: 250px;
	margin-top: 80px 15px;
	padding: 5px;
	flex: 0 0 250px;
	-ms-align-items: center;
	align-items: center;
	z-index: 5;

}

.bl3_2_pbl img {
	max-width: 100%;
	width: 65px;
	min-height: 65px;
	z-index: 5;
}

.bl3_2_pbl h4 {
	font-size: 22px;
	font-family: 'Exo', sans-serif;
	color: rgb(40, 40, 40); 
	margin: 15px 0;
	z-index: 4;
}






